@charset "UTF-8";
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "TTFirsNeue";
  src: url("../fonts/TTFirsNeue-Bold.woff2") format("woff2"), url("../fonts/TTFirsNeue-Bold.woff") format("woff");
  font-display: swap;
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: "TTFirsNeue";
  src: url("../fonts/TTFirsNeue-Medium.woff2") format("woff2"), url("../fonts/TTFirsNeue-Medium.woff") format("woff");
  font-display: swap;
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: "TTFirsNeue";
  src: url("../fonts/TTFirsNeue-Regular.woff2") format("woff2"), url("../fonts/TTFirsNeue-Regular.woff") format("woff");
  font-display: swap;
  font-weight: 400;
  font-style: normal;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}

html {
  font-family: "TTFirsNeue", Arial, Helvetica, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

a {
  text-decoration: none;
  color: inherit;
  list-style-type: none;
}

button {
  padding: 0;
  margin: 0;
  outline: none;
  box-shadow: none;
  font-size: 16px;
  line-height: 18.75px;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-size: 18px;
  color: var(--color-text-grey);
  background-color: var(--color-white);
  outline: none;
}

label {
  display: block;
}

h2 {
  font-size: 36px;
  line-height: 37.44px;
  font-weight: 700;
}

h3 {
  font-size: 24px;
  line-height: 30.96px;
  font-weight: 700;
}

p,
span,
a {
  font-size: 16px;
}

.title-reset {
  margin: 0;
}

.descr-reset {
  margin: 0;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.btn-reset {
  background-color: transparent;
  cursor: pointer;
  padding: 0px;
  border-width: initial;
  border-style: none;
  border-color: initial;
  border-image: initial;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0, 0, 0, 0);
  overflow: hidden;
}

.container {
  max-width: 1290px;
  width: 100%;
  margin: 0 auto;
}

.hashtag {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 7px 15px;
  font-size: 14px;
  line-height: 16px;
  color: #FFF;
  background-color: #333;
}

@media (max-width: 1300px) {
  .container {
    padding-left: 35px;
    padding-right: 35px;
  }
}
@media (max-width: 1024px) {
  .container {
    padding-left: 31px;
    padding-right: 31px;
  }
}
@media (max-width: 768px) {
  .container {
    padding-left: 27px;
    padding-right: 27px;
  }
}
@media (max-width: 650px) {
  .container {
    padding-left: 20px;
    padding-right: 20px;
  }

  .hashtag {
    top: 6px;
    right: 6px;
    padding: 4px 7px;
    font-size: 12px;
    line-height: 14px;
  }

  h3 {
    font-size: 18px;
    line-height: 23px;
  }
}
.header.fixed {
  /*закрепленная шапка*/
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 11;
  transform: translate3d(0, 0, 0);
  /*чтобы менюшка не дергалась при скроле*/
}

.header {
  min-width: 320px;
  background-color: #202020;
}
.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 22px;
  padding-bottom: 24px;
}
.header__nav-list {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #FFF;
}
.header__nav-item:not(:last-child) {
  margin-right: 74px;
}
.header__nav-item:first-child {
  display: none;
}
.header__nav-link {
  transition: all 0.3s ease-in-out;
}
.header__menu-burger {
  display: none;
  color: #808080;
}
.header__menu-burger-open {
  width: 20px;
  height: 18px;
  color: #FFF;
  transition: all 0.3s ease-in-out;
}
.header__menu-burger-close {
  margin-left: 5px;
  display: none;
  width: 18px;
  height: 18px;
  transition: all 0.3s ease-in-out;
  color: inherit;
  fill: inherit;
}

.logo {
  display: flex;
  justify-content: center;
}
.logo__img {
  margin-right: 11px;
  width: 30px;
  height: 30px;
}
.logo__img-inscription {
  margin-top: 5px;
  width: 147px;
  height: 28px;
}

.search__btn {
  display: none;
}
.search__open {
  display: none;
}
.search__open-svg {
  color: #FFF;
  width: 25px;
  height: 25px;
  transition: all 0.3s ease-in-out;
}
.search__close-svg {
  width: 23px;
  height: 23px;
  color: #FF6E30;
}

.social__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  max-width: 131px;
  margin: 0 auto;
}
.social__list-link {
  display: flex;
}
.social__list-link-svg {
  width: 30px;
  height: 30px;
}
.social__list-link svg {
  color: #CACACA;
  transition: all 0.3s ease-in-out;
}

@media (min-width: 1024px) {
  .header__nav-link:hover {
    color: #FF6E30;
  }

  .search__open:hover .search__open-svg {
    color: #FF6E30;
  }

  .social__list-link:hover svg {
    color: #FF6E30;
  }
}
.header__nav-link:active {
  color: #CE4307;
}
.header__nav-link:focus-visible {
  color: #FFF;
  background-color: #BC572B;
  outline: none;
  padding: 5px;
}
.header__menu-burger:active {
  color: #CE4307;
}
.header__menu-burger:focus-visible {
  color: #BC572B;
}
.header__menu-burger:active .header__menu-burger-open {
  color: #CE4307;
}
.header__menu-burger:focus-visible .header__menu-burger-open {
  color: #FF6E30;
  outline: none;
}

.search__open:active .search__open-svg {
  color: #CE4307;
}
.search__open:focus-visible .search__open-svg {
  color: #BC572B;
  outline: none;
}

.social__list-link:active svg {
  color: #CE4307;
}
.social__list-link:focus-visible svg {
  color: #BC572B;
}
.social__list-link:focus-visible {
  outline: none;
}

@media (max-width: 1300px) {
  .logo__img {
    width: 26px;
    height: 26px;
  }
  .logo__img-inscription {
    width: 128px;
    height: 24px;
  }

  .search {
    position: relative;
  }
  .search__open {
    display: block;
    padding-top: 5px;
  }
  .search__btn {
    padding-bottom: 5px;
    position: absolute;
    top: 50px;
    right: 60px;
    background-color: #202020;
  }
  .search__btn.open {
    display: flex;
  }
  .search__close {
    color: #FFF;
  }
  .search__btn-input {
    width: 300px;
    padding-left: 5px;
    font-size: 16px;
    line-height: 15px;
    background-color: #202020;
    border: 0;
    border-bottom: 1px solid #e9e9e9;
    color: #FFF;
    outline: none;
  }
  .search__btn-input::placeholder {
    font-size: 16px;
    color: #FFF;
  }

  .header__nav-item:not(:last-child) {
    margin-right: 33px;
  }

  .social {
    display: none;
  }
}
@media (max-width: 1300px) {
  .header-container {
    padding-left: 27px;
    padding-right: 27px;
  }
}
@media (max-width: 1024px) {
  .header-container {
    padding-left: 27px;
    padding-right: 27px;
  }
}
@media (max-width: 850px) {
  .header-container {
    padding-left: 27px;
    padding-right: 27px;
  }
  .header__nav-menu {
    width: 100%;
    min-width: 320px;
    padding-top: 11px;
    padding-bottom: 11px;
    padding-left: 27px;
    padding-right: 27px;
    position: absolute;
    top: 80px;
    left: 0;
    background-color: #4A4A4A;
  }
  .header__nav-item:not(:last-child) {
    margin-right: 15px;
  }
  .header__nav-list {
    justify-content: space-between;
  }
}
@media (max-width: 600px) {
  .header__nav-list {
    width: 100%;
    min-height: 100vh;
    position: absolute;
    display: none;
    padding-top: 10px;
    padding-left: 21px;
    background-color: #202020;
    top: 0;
    left: 0;
    bottom: 0;
    z-index: 11;
  }
  .header__nav-list.open {
    display: block;
    overflow: scroll;
  }
  .header__nav-item:first-child {
    display: block;
  }
  .header__nav-item {
    padding-top: 20px;
    opacity: 0;
    transform: translateY(100px);
  }
  .header__nav-item .header__menu-burger {
    margin-right: 5px;
  }
  .header__menu-burger {
    display: block;
  }
  .header__menu-burger-close {
    display: block;
  }

  .search__btn {
    top: 70px;
    right: 21px;
    z-index: 111;
  }
  .search__btn-input {
    width: 200px;
  }
}
@media (max-width: 450px) {
  .logo__img {
    width: 17px;
    height: 17px;
    margin-right: 6px;
  }
  .logo__img-inscription {
    margin-top: 0;
    width: 86px;
    height: 16px;
  }

  .header-container {
    padding-left: 21px;
    padding-right: 21px;
  }
  .header__nav-menu {
    top: 73px;
    padding: 11px 22px;
  }

  .search__open-svg {
    width: 20px;
    height: 20px;
  }
}
.intro-container {
  display: flex;
  justify-content: space-between;
}

.intro {
  padding-top: 41px;
  padding-bottom: 68px;
}
.intro__left {
  margin-right: 30px;
  max-width: 630px;
  max-height: 630px;
}
.intro__left-item {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  padding-left: 58px;
  padding-bottom: 60px;
  padding-right: 118px;
}
.intro__left-text {
  z-index: 1;
}
.intro__left-span {
  line-height: 18px;
  color: #FFF;
}
.intro__left-title {
  color: #FFF;
  padding-bottom: 19px;
}
.intro__left-button {
  padding: 15px 22px;
  background-color: #FF6E30;
  font-weight: 700;
  color: #FFF;
  border: 1px solid transparent;
  transition: all 0.3s ease-in-out;
}
.intro__left-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.intro__right-img {
  width: 100%;
  display: block;
}
.intro__right-header {
  line-height: 24.96px;
  padding-bottom: 18px;
}

.intro-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 30px;
}
.intro-grid__item-1 {
  grid-column: span 2;
}

@media (min-width: 1024px) {
  .intro__left-button:hover {
    background-color: #BC572B;
  }
}
.intro__left-button:active {
  background-color: #CE4307;
  border: 1px solid #CE4307;
}
.intro__left-button:focus-visible {
  background-color: #FF6E30;
  border: 1px solid #CE4307;
}

@media (max-width: 1300px) {
  .intro {
    padding-top: 34px;
    padding-bottom: 67px;
  }
  .intro__right-img {
    height: 100%;
  }
  .intro__right-header {
    padding-bottom: 26px;
  }
  .intro__left-item {
    padding-top: 25px;
    padding-left: 30px;
    padding-bottom: 30px;
    padding-right: 15px;
  }
  .intro__left-title {
    padding-top: 9px;
    padding-bottom: 20px;
    font-size: 34px;
    line-height: 34px;
  }

  .intro-grid {
    grid-template-columns: repeat(auto-fill, minmax(218px, 1fr));
  }
}
@media (max-width: 1024px) {
  .intro-grid {
    grid-template-columns: repeat(auto-fill, minmax(156px, 1fr));
  }

  .intro__left {
    max-width: 466px;
    max-height: 623px;
  }
}
@media (max-width: 850px) {
  .intro {
    padding-top: 82px;
  }
  .intro__right-img {
    height: 100%;
  }
  .intro__left-item {
    padding-left: 30px;
    padding-bottom: 30px;
  }

  .intro-grid {
    grid-template-columns: repeat(auto-fill, minmax(156px, 1fr));
  }
}
@media (max-width: 768px) {
  .intro-grid {
    grid-template-columns: repeat(auto-fill, minmax(110px, 1fr));
  }

  .intro__left {
    max-width: 342px;
    max-height: 452px;
  }
  .intro__left-item {
    padding-right: 66px;
  }
  .intro__right-header {
    padding-bottom: 17px;
    font-size: 22px;
    line-height: 22px;
  }
}
@media (max-width: 650px) {
  .intro-container {
    flex-direction: column;
  }

  .intro__left {
    max-width: 630px;
    margin-right: 0;
    margin-bottom: 34px;
  }
  .intro__left-item {
    min-height: 422px;
  }

  .intro-grid {
    grid-template-columns: repeat(auto-fill, minmax(218px, 1fr));
  }
}
@media (max-width: 500px) {
  .intro-grid {
    grid-template-columns: repeat(auto-fill, minmax(135px, 1fr));
  }

  .intro {
    padding-top: 70px;
    padding-bottom: 49px;
  }
  .intro__left-item {
    padding-right: 28px;
  }
  .intro__left-span {
    font-size: 14px;
  }
  .intro__left-title {
    padding-top: 8px;
    padding-bottom: 11px;
    width: 76%;
    font-size: 24px;
    line-height: 26px;
  }
  .intro__left-button {
    padding: 11px 22px;
    font-size: 14px;
  }
  .intro__right-header {
    font-size: 18px;
    line-height: 18px;
  }
}
@media (max-width: 320px) {
  .intro-grid {
    grid-template-columns: repeat(auto-fill, minmax(124px, 1fr));
  }
}
.about-us {
